@import '../../variables';

.navbar{
    &__wrapper{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: 10px;
        height: 60px;
        background: #0c0c0c;
        color: white;
        gap: 10px;
        flex-wrap: wrap;
    }
    &__link{
        position: relative;
        text-decoration: none;
        color: white;
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        &:hover{
            color: $main-color-green;
            transition: all .5s ease-in-out;
            &:after{
                content: '';
                position: absolute;
                left: 0;
                top: 100%;
                width: 100%;
                height: 2px;
                background-color: $main-color-green;
                -webkit-animation: mymove .5s;
                animation: mymove .5s ;
            }
        }
    }
    @-webkit-keyframes mymove {
        from {width: 0px;}
        to {width: 100%;}
    }
    
    @keyframes mymove {
        from {width: 0px;}
        to {width: 100%;}
    } 
}